@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.hero {
  position: relative;
  padding-top: calc(var(--header-height) * 1.5);
  padding-bottom: rem(30);

  &::before {
    content: "";
    position: absolute;
    right: -15%;
    top: -20%;
    width: 100%;
    z-index: -1;
    height: 100%;
    max-width: 1150px;
    max-height: 1150px;
    background-image: url("../images/gradients.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.3;

    @include media(mobile-m) {
      right: 0;
    }
  }

  &__inner {
    @include flex-all-center;

    height: calc(100vh - calc(var(--header-height) * 1.5));
    height: calc(var(--vh, 1vh) * 100 - calc(var(--header-height) * 1.5));

    @include media(mobile-m) {
      flex-direction: column;
    }
  }

  &__content {
    @include font-size (25, 34);

    margin-right: auto;
    width: 100%;
    max-width: 525px;

    >* {
      @include media(mobile-m) {
        margin: 0 auto;
      }
    }

    >img {
      margin-bottom: 20px;

      @include media(tablet) {
        max-width: 375px;
      }

      @include media(mobile-m) {
        max-width: 270px;
      }
    }

    p {
      margin-bottom: rem(37);
      letter-spacing: 0.005em;

      span {
        color: var(--color-violet-400);
      }

      @include media(mobile-l) {
        width: 65%;
      }
    }

    @include media(mobile-l) {
      @include font-size (19, 31);

      transform: translateY(-13%);
    }

    @include media(mobile-m) {
      max-width: none;
      text-align: center;
      transform: translate(0);
    }
  }

  &__picture {
    margin-right: auto;
    width: 100%;
    max-width: 367px;
    transform: translate(-68px, -24px);

    img {
      @include media(mobile-m) {
        display: none;
      }
    }

    @include media(desktop-wide) {
      margin-right: 0;
      transform: translate(-30px, 0);
    }

    @include media(tablet) {
      max-width: 300px;
      transform: translate(0, 0);
    }

    @include media(mobile-l) {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -1;
      max-width: 270px;
    }

    @include media(mobile-m) {
      position: relative;
      max-width: none;
    }
  }

  &__picture-desc {
    @include font-size (15, 20);

    position: absolute;
    right: -77%;
    top: 45px;
    border-radius: var(--radius-btn);
    padding: 24px 19px;
    width: 100%;
    max-width: 307px;
    background-color: var(--color-white);

    span {
      font-weight: 700;
      color: var(--color-violet-400);
    }

    @include media(desktop-wide) {
      right: 60%;
    }

    @include media(tablet) {
      right: 30%;
      top: -20px;
    }

    @include media(mobile-l) {
      right: 15px;
      top: auto;
      bottom: 20%;
    }

    @include media(mobile-m) {
      position: relative;
      left: auto;
      right: auto;
      bottom: auto;
      margin: 0 auto;
      max-width: 320px;
      text-align: center;
    }
  }

  .button {
    max-width: 307px;
  }
}