@use "../utils/functions/"as*;
@use "../utils/mixins"as*;

.logo {
  display: inline-flex;
  width: 100%;

  img {
    width: 100%;
  }
}

.header {
  .logo {
    border-radius: 0 0 var(--radius-main) var(--radius-main);
    padding: 14px 25px;
    max-width: 146px;
    background-color: var(--color-violet-400);

    @include media(mobile-m) {
      padding: 10px;
      max-width: 120px;
    }
  }
}

.footer {
  .logo {
    margin-right: 30px;
    max-width: 240px;

    @include media(mobile-l) {
      max-width: 180px;
    }

    @include media(mobile-s) {
      margin: 0 auto 30px;
      max-width: 200px;
    }
  }
}