@use "../utils/functions/"as*;
@use "../utils/mixins"as*;

.product {
  @include flex;

  position: relative;

  &__content {
    margin-right: 20px;
    margin-left: 8px;
    width: 100%;
    max-width: 695px;

    @include media(mobile-l) {
      margin: 0;
      max-width: none;
    }
  }

  &__title {
    margin-bottom: rem(53);

    @include media(tablet) {
      margin-bottom: 30px;
    }
  }

  &__desc {
    @include font-size(18, 24);

    max-width: 532px;

    p {
      &:not(:last-child) {
        margin-bottom: 30px;
      }

      &:last-of-type {
        margin-bottom: 78px;

        @include media(tablet) {
          margin-bottom: 30px;
        }
      }
    }
  }

  &__picture {
    margin-left: auto;
    width: 100%;
    max-width: 360px;
    transform: translate(7px, 3px);

    img {
      width: 100%;
    }

    @include media(tablet-wide) {
      transform: translate(0, 0);
    }

    @include media(mobile-l) {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: -1;
      max-width: 260px;
      opacity: 0.2;
    }
  }

  &--reverse {
    flex-direction: row-reverse;

    .product {
      &__picture {
        transform: translate(-15%, -8%);

        @include media(tablet) {
          margin-right: 20px;
          transform: translate(0, 0);
        }

        @include media(mobile-l) {
          left: 0;
          right: auto;
          margin: 0;
          max-width: 230px;
        }
      }

      &__content {
        max-width: 636px;
      }

      &__desc {
        max-width: 586px;
      }
    }
  }

  @include media(tablet) {
    align-items: center;
  }
}