@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.write {
  @include flex-all-sb;

  margin-bottom: rem(100);
  border-radius: var(--radius-btn);
  padding: rem(58) rem(42) rem(40);
  background-color: var(--color-white);

  &__icon {
    svg {
      height: 105px;
      max-width: 105px;

      @include media(mobile-m) {
        height: 70px;
        max-width: 70px;
      }
    }
  }

  &__text {
    @include font-size(18, 24);

    width: 100%;
    max-width: 495px;

    p {
      margin-right: 25px;
    }
  }

  &__btn {
    position: relative;
    width: 100%;
    max-width: 250px;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -50%;
      z-index: 0;
      border-radius: 10px;
      width: 100%;
      height: 100%;
      background: #d9d9d9;
      filter: blur(27px);
    }
  }

  .button {
    @include font-size(17, 25);

    position: relative;
    z-index: 1;
  }

  .title {
    @include font-size(35, 45);

    margin-bottom: rem(38);

    @include media(mobile-l) {
      @include font-size(28, 38);
    }
  }

  @include media(tablet) {
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr;
    margin: 0 auto 50px;
    padding: 20px;
    text-align: center;
    gap: 30px;
  }

  @include media(mobile-l) {
    max-width: 576px;
  }

  @include media(mobile-s) {
    padding: 20px 0;
  }
}