@use "../utils/functions/"as*;
@use "../utils/mixins/"as*;

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
}

address {
  @include font-size(25, 43);

  @include media(mobile-s) {
    @include font-size(20, 31);
  }
}

.both {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    right: -25%;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    max-width: 1150px;
    max-height: 1150px;
    background-image: url("../images/gradients.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.3;

    @include media(mobile-m) {
      background-size: 100% 100%;
    }
  }

  &--bottom {
    &::before {
      top: auto;
      bottom: -7%;
      max-width: none;
      max-height: none;

      @include media(tablet) {
        background-size: contain;
      }
    }
  }
}