@font-face {
  src: url("kharkiv-tone.4b46735e.woff2") format("woff2");
  font-family: Kharkiv Tone;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

:root {
  --font-family-primary: "Montserrat", sans-serif;
  --font-family-secondary: "Rubik", sans-serif;
  --font-family-titles: "Kharkiv Tone", sans-serif;
  --content-width: 1110px;
  --content-wide: 1300px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --container-wide: calc(var(--content-wide)  + (var(--container-offset) * 2));
  --container-wider: calc(var(--content-wide)  + 20px + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-black: #000;
  --color-dark-300: #1d1b1b;
  --color-dark-350: #0b132a;
  --color-dark-400: #131314;
  --color-dark-500: #0f0e0e;
  --color-gray-200: #f8f8f8;
  --color-gray-400: #afacac;
  --color-gray-300: #eeeff2;
  --color-blue-800: #0c224a;
  --color-blue-700: #0421a9;
  --color-violet-400: #2f48bc;
  --radius-main: 15px;
  --radius-btn: 10px;
  --shadow: 4px 4px 10px #00000040;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

.page {
  font-family: var(--font-family-primary);
  letter-spacing: 0;
  color: var(--color-dark-400);
  background: var(--color-white);
  scroll-padding-top: var(--header-height, 80px);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.35;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
}

.page__body:before {
  content: "";
  left: -100%;
  top: var(--header-height);
  z-index: 1;
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  background-color: var(--color-violet-400);
  opacity: .5;
  transition: left .2s;
  position: fixed;
}

.page--menu .page__body:before {
  left: 0;
}

.page--menu .nav {
  right: 0;
}

.page--menu .header.hide {
  transform: translate(0);
}

.page--menu.scroll {
  overflow: hidden;
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.container--wide {
  max-width: var(--container-wide);
}

.container--wider {
  max-width: var(--container-wider);
}

.main {
  min-height: calc(100vh - var(--header-height) * 2 + 47px);
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.dis-scroll {
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

address {
  letter-spacing: normal;
  font-size: 25px;
  line-height: 1.72;
}

@media only screen and (max-width: 475px) {
  address {
    letter-spacing: normal;
    font-size: 20px;
    line-height: 1.55;
