}

.both {
  position: relative;
}

.both:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  max-width: 1150px;
  max-height: 1150px;
  opacity: .3;
  background-image: url("gradients.99b1e02b.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  right: -25%;
}

@media only screen and (max-width: 576px) {
  .both:before {
    background-size: 100% 100%;
  }
}

