@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.list {
  position: relative;
  padding-bottom: rem(95);

  ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include media(mobile-l) {
      grid-template-columns: 1fr;
      margin: 0 auto;
      max-width: 475px;
    }
  }

  li {
    border-radius: calc(var(--radius-btn) * 2.4);
    padding: rem(50);
    box-shadow: var(--shadow);
    background-color: var(--color-white);

    div {
      @include flex-v-center;

      margin-bottom: rem(60);

      @include media(mobile-l) {
        margin-bottom: 30px;
      }
    }

    h3 {
      @include font-size(38, 51);

      width: 100%;
      max-width: 330px;
      font-family: var(--font-family-titles);
      color: var(--color-violet-400);

      @include media(tablet-wide) {
        @include font-size(34, 44);
      }

      @include media(tablet) {
        @include font-size(28, 38);
      }

      @include media(mobile-s) {
        @include font-size(25, 34);
      }
    }

    svg {
      width: 50px;
      height: 50px;
    }

    p {
      @include font-size(18, 24);
    }

    &:first-child {
      svg {
        width: 88px;
        height: 58px;
      }
    }

    &:nth-child(2) {
      svg {
        width: 63px;
        height: 63px;
      }
    }

    &:nth-child(3) {
      svg {
        width: 71px;
        height: 83px;
      }
    }

    &:nth-child(4) {
      svg {
        width: 80px;
        height: 80px;
      }
    }

    @include media(tablet) {
      padding: 35px;
    }

    @include media(mobile-l) {
      padding: 25px;
    }

    @include media(mobile-s) {
      padding: 20px;
    }
  }

  @include media(mobile-l) {
    padding-bottom: 60px;
  }
}