@use "../utils/mixins/"as *;
@use "../utils/functions/"as *;

.header {
  position: fixed;
  top: 0;
  z-index: 3;
  width: 100%;
  transition: transform 0.15s, background-color 0.15s;

  &__inner {
    @include flex-all-sb;
  }

  &.hide {
    transform: translateY(-110%);
  }

  &.scroll {
    overflow: inherit;
    background-color: var(--color-white);
  }

  @include media(tablet) {
    background-color: var(--color-white);
  }
}