@use "../utils/functions/"as*;
@use "../utils/mixins"as*;

.page {
  font-family: var(--font-family-primary);
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 1.35;
  letter-spacing: 0;
  color: var(--color-dark-400);
  background: var(--color-white);
  scroll-padding-top: var(--header-height, 80px);

  &__body {
    min-width: 320px;
    min-height: 100%;

    &::before {
      content: "";
      position: fixed;
      left: -100%;
      top: var(--header-height);
      z-index: 1;
      width: 100vw;
      height: 100vh;
      height: calc(var(--vh, 1vh) * 100);
      background-color: var(--color-violet-400);
      opacity: 0.5;
      transition: left 0.2s;
    }
  }

  &--menu {
    .page__body {
      &::before {
        left: 0;
      }
    }

    .nav {
      right: 0;
    }

    .header {
      &.hide {
        transform: translate(0);
      }
    }

    &.scroll {
      overflow: hidden;
    }
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);

  &--wide {
    max-width: var(--container-wide);
  }

  &--wider {
    max-width: var(--container-wider);
  }
}

.main {
  @include footerToBottom;
}