@use "../utils/mixins/"as*;
@use "../utils/functions/"as*;

.footer {
  padding: rem(38) 0;
  background-color: var(--color-gray-200);

  &__inner {
    @include flex-all-sb;

    align-items: flex-end;
    margin-bottom: rem(60);

    p {
      margin-bottom: -11px;
      width: 100%;
      max-width: 765px;

      @include media(tablet) {
        max-width: 400px;
      }
    }

    @include media(tablet) {
      align-items: center;
      justify-content: space-between;
    }

    @include media(mobile-s) {
      flex-direction: column;
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: 330px 1fr;
    gap: 0 128px;

    @include media(tablet) {
      gap: 50px;
    }

    @include media(mobile-l) {
      grid-template-columns: 1fr;
    }
  }

  &__nav {
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(3, 100px);
    font-family: var(--font-family-secondary);
    gap: 0 40px;

    p {
      margin-bottom: 20px;
      font-weight: 500;
    }

    div {
      &:last-child {
        @include media(mobile-sm) {
          justify-self: center;
          grid-column: 1 / span 2;
        }
      }
    }

    li {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    a {
      @include font-size(16, 30);
    }

    @include media(tablet) {
      gap: 0 20px;
    }

    @include media(mobile-l) {
      justify-self: center;
      gap: 0 60px;
    }

    @include media(mobile-s) {
      gap: 40px 20px;
    }

    @include media(mobile-sm) {
      justify-content: space-between;
      justify-items: center;
      grid-template-columns: repeat(2, 1fr);
      width: 100%;
    }
  }

  .title {
    @include font-size(30, 41);

    @include media(tablet) {
      @include font-size(24, 35);
    }

    @include media(mobile-l) {
      @include font-size(19, 31);
    }

    @include media(mobile-s) {
      text-align: center;
    }
  }

  address {
    @include media(mobile-l) {
      justify-self: center;
    }
  }
}