@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.products {
  position: relative;
  margin-bottom: 130px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: -50%;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-image: url("../images/gradients.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0.3;
  }

  &::after {
    top: 40%;
  }

  h2 {
    @include font-size(70, 95);

    margin-bottom: rem(67);
    text-align: center;

    @include media(desktop) {
      @include font-size(58, 70);
    }

    @include media(tablet-wide) {
      @include font-size(50, 60);
    }

    @include media(tablet) {
      @include font-size(42, 52);

      margin-bottom: 40px;
    }

    @include media(mobile-l) {
      @include font-size(36, 48);

      margin-bottom: 30px;
    }
  }

  .product {
    &:not(:last-child) {
      margin-bottom: 230px;

      @include media(tablet-wide) {
        margin-bottom: 170px;
      }

      @include media(tablet) {
        margin-bottom: 90px;
      }
    }

    &__title {
      margin-bottom: 25px;
    }

    &__content {
      max-width: 570px;
    }

    &__desc {
      p {
        &:last-of-type {
          margin-bottom: rem(35);
          font-weight: 500;
        }
      }
    }

    &__picture {
      transform: translate(21%, 4%);

      @include media(tablet) {
        max-width: 280px;
        transform: translate(0, 0);
      }
    }

    .button {
      max-width: 252px;
      background-color: var(--color-blue-700);
    }
  }

  @include media(tablet) {
    margin-bottom: 100px;
  }
}