:root {
  // base
  --font-family-primary: "Montserrat", sans-serif;
  --font-family-secondary: "Rubik", sans-serif;
  --font-family-titles: "Kharkiv Tone", sans-serif;
  --content-width: 1110px;
  --content-wide: 1300px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-wide: calc(var(--content-wide) + (var(--container-offset) * 2));
  --container-wider: calc(var(--content-wide) + 20px + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-black: #000;
  --color-dark-300: #1d1b1b;
  --color-dark-350: #0b132a;
  --color-dark-400: #131314;
  --color-dark-500: #0f0e0e;
  --color-gray-200: #f8f8f8;
  --color-gray-400: #afacac;
  --color-gray-300: #eeeff2;
  --color-blue-800: #0c224a;
  --color-blue-700: #0421a9;
  --color-violet-400: #2f48bc;

  // radius
  --radius-main: 15px;
  --radius-btn: 10px;

  //shadow
  --shadow: 4px 4px 10px rgba(0, 0, 0, 0.25);
}