@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.contact {
  margin-bottom: rem(122);
  border-radius: var(--radius-btn);
  padding: 43px;
  background-color: #fff;

  .title {
    @include font-size(40, 53);

    margin-bottom: 25px;
    text-align: center;
    text-transform: uppercase;

    @include media(tablet) {
      @include font-size(34, 43);
    }

    @include media(mobile-m) {
      @include font-size(28, 37);
    }
  }

  p {
    @include font-size(18, 24);

    margin: 0 auto 42px;
    width: 100%;
    max-width: 530px;
    text-align: center;
  }

  form {
    @include flex;

    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    max-width: 608px;

    label {
      display: block;
      margin-bottom: 5px;
      text-transform: uppercase;
    }

    input,
    textarea {
      @include font-size(15, 20);

      border: 1px solid rgba(165, 165, 165, 0.7);
      padding: 10px 20px;

      &:not(:last-child, textarea) {
        margin-bottom: 21px;
      }
    }

    textarea {
      display: block;
      margin-bottom: 44px;
      max-height: 125px;
      resize: none;
    }

    .button {
      margin: 0 auto;
      max-width: 250px;
      text-transform: uppercase;
    }
  }

  @include media(mobile-l) {
    margin: 0 auto 50px;
    padding: 20px;
    max-width: 576px;
  }

  @include media(mobile-m) {
    padding: 20px 0;
  }
}