@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.about {
  position: relative;
  margin-bottom: 56px;
  padding: 43px 0 260px;

  &::before {
    content: "";
    position: absolute;
    right: -8%;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-image: url('../images/offer.svg');
    background-position: center top;
    background-size: auto;
    background-repeat: no-repeat;

    @include media(desktop) {
      background-position: center;
      background-size: cover;
    }

    @include media(mobile-l) {
      display: none;
    }
  }

  .button {
    @include font-size(20, 29);

    border-radius: calc(var(--radius-btn) * 2.2);
    padding: 19px rem(40);
    font-weight: 500;
  }

  .product {
    &__desc {
      p {
        &:first-child {
          @include font-size(25, 40);
        }
      }
    }

    &__picture {
      @include media(mobile-l) {
        bottom: -20%;
      }
    }
  }

  @include media(desktop) {
    padding: 43px 0 220px;
  }

  @include media(tablet-wide) {
    padding: 43px 0 160px;
  }
}