@use "../../utils/functions/"as*;
@use "../../utils/mixins"as*;

.advantages {
  position: relative;
  margin-bottom: rem(120);

  ul {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-radius: var(--radius-btn);
    padding: 35px 5px 35px 90px;
    background-color: var(--color-white);
    gap: 30px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -50%;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: #2d409a;
      opacity: 0.06;
      filter: blur(60px);
    }

    @include media(desktop) {
      padding: 20px 50px;
    }

    @include media(tablet) {
      justify-items: center;
      grid-template-columns: repeat(2, 1fr);
    }

    @include media(mobile-l) {
      grid-template-columns: 1fr;
    }

    @include media(mobile-s) {
      padding: 20px 10px;
    }
  }

  li {
    @include flex-v-center;

    position: relative;
    padding: 30px 0;

    &::before {
      content: "";
      position: absolute;
      right: 12px;
      top: 0;
      width: 2px;
      height: 100%;
      background-color: var(--color-gray-300);

      @include media(desktop) {
        right: 0;
      }

      @include media(tablet) {
        right: -15px;
      }

      @include media(mobile-l) {
        top: auto;
        bottom: 0;
        width: 100%;
        height: 1px;
      }
    }

    svg {
      margin-right: 30px;
      fill: var(--color-violet-400);
    }

    p {
      @include font-size(16, 22);

      width: 100%;
      max-width: 170px;

      span {
        font-weight: 700;
      }
    }

    &:last-child {
      &::before {
        display: none;

        @include media(tablet) {
          display: block;
          width: 100%;
          height: 1px;
        }

        @include media(mobile-l) {
          top: 0;
          bottom: auto;
        }
      }

      @include media(tablet) {
        grid-column: 1 / span 2;
      }

      @include media(mobile-l) {
        grid-column: auto;
      }
    }

    &:nth-child(2) {
      &::before {
        @include media(tablet) {
          display: none;
        }
      }
    }

    &:nth-child(1) {
      svg {
        width: 100%;
        height: 66px;
        max-width: 56px;
      }
    }

    &:nth-child(2) {
      svg {
        width: 100%;
        height: 47px;
        max-width: 70px;
      }
    }

    &:nth-child(3) {
      svg {
        width: 100%;
        height: 66px;
        max-width: 30px;
      }
    }

    @include media(tablet) {
      justify-content: center;
      width: 100%;
    }
  }

  @include media(tablet) {
    margin-bottom: 70px;
  }

  @include media(mobile-l) {
    margin-bottom: 40px;
  }

  @include media(mobile-s) {
    margin-bottom: 20px;
  }
}